




















































import { Component, Vue } from 'vue-property-decorator'
import { Chapter1 } from '@/hycom_lib/chapter1'
import { Table2DLerp } from '@/hycom_lib/common'

import TablePane from './components/TablePane.vue'
import HycomForm from '@/components/HycomForm/index.vue'

@Component({
  components: {
    HycomForm,
    TablePane
  }
})
export default class Chapter4Section4Sub2 extends Vue {
  public title = '4.4.2 高流速水工隧洞沿程水流空化数';
  public initData = {
    '\\nabla': 1862,
    h_i: '13.80776;9.518;10.923',
    v_i: '20.912;24.27;21.148',
    C: 15
  };
  public data = {
    '\\nabla': '',
    h_i: '',
    v_i: '',
    C: ''
  };

  public formulas = {
    0: '$$\\sigma_i :第i个断面水流空化数;$$',
    1: '$$h_i :第i个计算断面处的动水压力水头,水柱高,m$$',
    2: '$$h_a :第i个计算断面处的大气压力水头,水柱高,m$$',
    3: '$$h_v :第i个断面水的汽化压力水头,水柱高,m$$',
    4: '$$v_i :第i个计算断面的平均流速,m$$',
    5: '$$\\nabla :计算断面高程,m$$',
    6: '$$C :水温$$'
  };

  public demoContent =
    '国内某水电站,水库校核水位1860.4m,正常蓄水位1856m,汛限水位1854m,中孔泄洪洞进水口为有压短管,有压短管出口高程804m,后接“龙抬头”无压泄洪洞,隧洞为城门洞形,隧洞断面尺寸为10mx15m,底坡为0.14,洞长206.39m,反弧末段高程1778.007m,反弧段后底坡为0.00472、洞长442.937m。进水口底板高程1804m,工作闸门尺寸8mx11m,流量系数μ=0.871,垂直收缩系数ε=0.914,混凝土衬砌糙率0.014,校核工况下泄量2310m3/s,求校核洪水时中孔泄洪洞沿程水面线（空化数）。';
  public demoResult = '断面1:水流空化数=0.58;不需要设掺气坎|断面2:水流空化数=0.29;需要设掺气坎|断面3:水流空化数=0.44;不需要设掺气坎|';

  // -------table 251 data--------------------------
  public table251Data = [
    [0.06, 0.09, 0.13, 0.17, 0.24, 0.32, 0.43, 0.75]
  ];
  public table251Vertical = [0];
  public table251Horizon = [0, 5, 10, 15, 20, 25, 30, 40];

  public tableOptions = [
    { label: '表:水的汽化压力水头hv与水温的关系', key: 't1' }
  ];
  public activeTableName = 't1';
  // -------table 251 data--------------------------

  get result() {
    let outStr = ''
    let tableLerp = new Table2DLerp()
    let hv = tableLerp.arr(
      this.table251Vertical,
      this.table251Horizon,
      this.table251Data,
      0,
      +this.data.C
    )

    if (this.data.h_i.toString().includes(';') && this.data.v_i.toString().includes(';')) {
      let hiArray = this.data.h_i.toString().split(';')
      let viArray = this.data.v_i.toString().split(';')
      if (hiArray.length === viArray.length) {
        for (let index = 0; index < hiArray.length; index++) {
          const vi = viArray[index]
          const hi = hiArray[index]

          let objs = Chapter1.slkqhs(
            +this.data['\\nabla'],
            +hi,
            hv,
            +vi
          )
          outStr += '断面' + (index + 1).toString() + ':水流空化数='
          outStr += objs.sigma.toFixed(2).toString() + ';' + objs.out + '|'
        }
      }
    } else if (!this.data.h_i.toString().includes(';') && !this.data.v_i.toString().includes(';')) {
      let objs = Chapter1.slkqhs(
        +this.data['\\nabla'],
        +this.data.h_i,
        hv,
        +this.data.v_i
      )
      outStr = '空化数:'
      outStr += objs.sigma.toFixed(2).toString() + '|' + objs.out
    }

    return outStr
  }
}
